@import './variables/variables.scss';


#root {
  background: url('./img/background.jpg') no-repeat;
  background-size: cover;
  height:100vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 20px;
}

body {
  margin: 0;
 background-image:linear-gradient(90deg,hsl(224deg 44% 12%) 0%,hsl(188deg 64% 26%) 69%,hsl(151deg 90% 36%) 100%);
}

.app {
  max-width: 1366px;
  width: 100%;
  height: 768px;
  background: url('./img/background3.jpg') no-repeat;
  background-position: right;
  background-size: cover;
  padding: 0 20px;
  border-radius: 20px;
  box-shadow: 0px 0px 28px 14px #000;
  box-sizing: border-box;
  opacity: 0.8;
}

