#root{background:url(/CVWebSite/static/media/background.04c6af37.jpg) no-repeat;background-size:cover;display:flex;align-items:center;justify-content:center;padding:0 20px}body{margin:0;background-image:linear-gradient(90deg,hsl(224deg 44% 12%) 0%,hsl(188deg 64% 26%) 69%,hsl(151deg 90% 36%) 100%);}.app{max-width:1366px;width:100%;height:768px;background:url(/CVWebSite/static/media/background3.8466c21f.jpg) no-repeat;background-position:right;background-size:cover;padding:0 20px;border-radius:20px;box-shadow:0px 0px 28px 14px #000;box-sizing:border-box;opacity:0.8; height: 100vh}

.about{display:flex;justify-content:space-between;align-items:center;font-family:"Poppins",sans-serif;grid-gap:20px;gap:20px;margin-bottom:20px}.about__content{width:60%}.about__photo{width:224px;height:248px;background:url(/CVWebSite/static/media/resumeAVA.f5e71ac2.jpeg) no-repeat;background-size:cover;border-radius:20px;flex-shrink:0}.about__text{display:flex;flex-direction:column;font-weight:500;font-size:18px;color:#fff;width:70%}.about__title{font-size:40px;font-weight:700;color:#37cc19;margin:0 0 24px 0}.about__btn{display:inline-block;text-align:center;max-width:200px;width:100%;padding:10px;margin-top:15px;text-decoration:none;color:#fff;border:2px solid #fff;border-radius:20px;transition:.3s}.about__btn:hover{border:2px solid #37cc19;color:#37cc19}@media screen and (max-width: 768px){.about{flex-direction:column-reverse;align-items:center}.about__content{width:100%;display:flex;flex-direction:column;align-items:center}.about__text{width:100%;font-size:14px;background-color:#000;padding:20px 0;border-radius:20px;box-shadow:0px 0px 10px 10px #000000;margin:0}.about__title{font-size:18px;background-color: #000;box-shadow: 0 0 15px 14px #000;}.about__btn{font-size:14px;background-color:#000}}

.header{display:flex;justify-content:space-between;align-items:center;grid-gap:20px;gap:20px;height:53px;margin-bottom:20px}.header__navbar{display:flex;list-style-type:none;padding:0;margin:0;grid-gap:30px;gap:30px}.header__text{font-family:"Poppins",sans-serif;font-size:20px;font-weight:700;color:#fff}.header__link>a{text-decoration:none;color:#fff;font-family:"Poppins",sans-serif;font-size:18px;font-weight:500;padding:14px;transition:border-bottom .3s;border-bottom:2px solid transparent}.header__link>a:hover{border-bottom:2px solid #37cc19}@media screen and (max-width: 768px){.header{justify-content:center}.header__text{display:none}.header__navbar{grid-gap:0;gap:0;justify-content:space-between}.header__link>a{font-size:14px}}.active{color:#37cc19 !important;border-bottom:2px solid #37cc19 !important}

.skills{color:#fff;font-family:"Poppins",sans-serif}.skills__list{margin:0;padding:0}.skills__list li{list-style-type:none;display:flex;flex-direction:column;font-size:18px}.skills__list li:not(:last-child){margin-bottom:18px}.skills__title{font-size:28px;color:#37cc19;margin:0 0 4px 0}@media screen and (max-width: 768px){.skills__title{font-size:18px}.skills__list li{font-size:14px}}

.projects__recent{margin-bottom:20px}.projects__title{font-family:"Poppins",sans-serif;font-size:28px;color:#37cc19;margin:0 0 4px 0}.projects__list{margin:0;padding:0}.projects__link{text-decoration:none;font-family:"Poppins",sans-serif;font-size:20px;color:#fff;transition:color .3s;position:relative}.projects__link:hover{color:#37cc19}.projects__link:hover::after{content:'<';position:absolute;top:1px}@media screen and (max-width: 768px){.projects__title{font-size:18px}.projects__link{font-size:14px}}

.contacts>a{position:relative;transition:color .3s}.contacts>a:hover{color:#37cc19}.contacts>a:hover::after{content:'<';position:absolute;top:1px}.contacts__title{font-family:"Poppins",sans-serif;font-size:28px;color:#37cc19;margin:0 0 4px 0;line-height:28px}.contacts__text{display:inline-block;text-decoration:none;font-family:"Poppins",sans-serif;font-size:20px;color:#fff;margin-bottom:18px}@media screen and (max-width: 768px){.contacts__title{font-size:18px}.contacts__text{font-size:14px}}

